#searchForm{
    border: solid 2px rgb(199, 198, 198);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
   
}



#searchForm Button {
    height: 60px;
    width: 150px;
